import {
	mapState,
	mapMutations
} from 'vuex'
import mInput from '../../components/m-input.vue'
export default {
	components: {
		mInput
	},
	data() {
		return {
			account: '',
			password: '',
			verCode: '',
			countdown: '获取验证码',
			disabled: false,
			timestatus: false,
			clear: ''
		}
	},
	methods: {
		updatePwd() {
			/* 提交修改密码的请求 */
		},
		// 获取验证码
		getCode() {
			var that = this;
				that.disabled = true; //禁用点击
				var isSuccess = true;//假设校验手机号  和 请求验证码成功
				
				if(isSuccess){
					that.countdown = 60;
					that.timestatus = true;
					that.clear = setInterval(that.countDown, 1000);
				}else{
					that.disabled = false; //获取失败开启点击
				}
				
		},
		// 倒计时
		countDown() {
			var that = this;
			if (!that.countdown) {
				that.disabled = false;
				that.timestatus = false;
				that.countdown = '获取验证码';
				clearInterval(that.clear);
			} else {
				--that.countdown;
			}
		}

	}
}
